import React from "react";
import Link from "../../components/Link";

import "./styles.scss";

const StripeBuyButton = (props) => {
    const {price, stripeUrl} = props;
    return (
        <Link className="StripeBuyButton" href={stripeUrl} useTheming={false}>Buy | ${props.price}</Link>
    )
}

export default StripeBuyButton;