import * as React from "react";

import {cn} from "../../../classnames";

import "./styles.scss";

const header = (type) => {
    return (props) => {
        switch (type) {
            case 'h1':
                return <h1 className={cn(props, "HeadingText H1")}>{props.children}</h1>
            case 'h2':
                return <h2 className={cn(props, "HeadingText H2")}>{props.children}</h2>
            case 'h3':
                return <h3 className={cn(props, "HeadingText H3")}>{props.children}</h3>
            case 'h4':
                return <h4 className={cn(props, "HeadingText H4")}>{props.children}</h4>
            case 'h5':
                return <h5 className={cn(props, "HeadingText H5")}>{props.children}</h5>
            case 'h6':
                return <h6 className={cn(props, "HeadingText H6")}>{props.children}</h6>
            default:
                return <p  className={cn(props, "HeadingText")}>{props.children}</p>
        }
    };
};

export const H1 = header('h1');
export const H2 = header('h2');
export const H3 = header('h3');
export const H4 = header('h4');
export const H5 = header('h5');
export const H6 = header('h6');