import React from "react";

export function CommonHead(props) {
    const description = props?.data?.page?.description || 
        "Find all the best crochet products here at the Crochet Nest!";

    return (
        <>
            <meta name="theme-color" content="#ffdbdf" />
            <title>{props.data.page ? props.data.page.name : "The Crochet Nest"}</title>

            <meta name="description" content={description} />
        </>
    )
}

export default CommonHead;